import React from "react";
import { Nav } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";
import videoNewsfield from "../../../vid/newsfield.mp4";


const Newsfield = () => {
  return (
    <React.Fragment>
      <div className="container">
        <span id="nyhetsfelt"></span>
        <h1 className="text-start">Nyhetsfelt</h1>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <div className="subpage_container w-100">
                  <div className="row">
                    <div className="col-md-3">
                      <p className="text-start">
                      Her gjorde jeg det mulig  raskt legge til ny artikkel i nyhetsfelt via modalvindu uten  laste hele siden p nytt. 
                      I stedet for  ta omveien innom et administrasjonspanelet, kunne man da redigere direkte via CKEditor, inkludert en knapp for  laste opp filer
                      og en grei forhndsvisning av artikkelen. Alt p samme plass bare et klikk unna, ned fra tidligere fem klikk.
                      </p>
                    </div>
                    <div className="col-md-9">
                      <video className="embed-responsive-item ratio ratio-1x1" controls autoplay muted>
                        <source src={videoNewsfield} type="video/mp4" />
                        Beklager, nettleseren din sttter ikke video.
                      </video>

                    </div>
                    <div className="col-md-12 backLink">
                      <React.Fragment>
                        <LinkContainer to="/portfolio/projects/">
                          <Nav.Link className="link-primary" href="#projects">
                            G tilbake
                          </Nav.Link>
                        </LinkContainer>
                      </React.Fragment>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Newsfield;