import React from "react";
import { Nav } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";
import videoShoppinglist from "../../../vid/react-native-shoppinglist.mp4";


const Shoppinglist = () => {
  return (
    <React.Fragment>
      <div className="container">
        <span id="nyhetsfelt"></span>
        <h1 className="text-start">Handleliste-app i React Native</h1>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <div className="subpage_container w-100">
                  <div className="row">
                    <div className="col-md-9">
                      <p className="text-start">
                      Her har jeg brukt react-native-gesture-handler versjon 2 og react-native-reanimated versjon 3 for  f til en flytende "dra og slipp"-opplevelse. 
                      Trykk og hold nede ikonet til hyre for  dra en vare opp eller ned, alt skjer i UI-trden, slik at vi unngr potensiell lagg via JS-trden.
                      <br/>
                      <br/>
                      Handlelisten lagres lokalt via SQLite, og synkroniseres ved hjelp av Redux til Supabase Postgres database nr man er online. 
                      Legg til nye varer via knapp, eller slett varer via swipe til venstre. Kildekoden er forelpig privat p github, men utsnitt kan vises p oppfordring.
                      </p>
                    </div>
                    <div className="col-md-3">
                      <video className="embed-responsive-item ratio ratio-1x1" controls autoplay muted>
                        <source src={videoShoppinglist} type="video/mp4" />
                        Beklager, nettleseren din sttter ikke video.
                      </video>

                    </div>
                    <div className="col-md-12 backLink">
                      <React.Fragment>
                        <LinkContainer to="/portfolio/projects/">
                          <Nav.Link className="link-primary" href="#projects">
                            G tilbake
                          </Nav.Link>
                        </LinkContainer>
                      </React.Fragment>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Shoppinglist;